@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/PTMono-regular.woff2") format("woff2"), url("../fonts/PTMono-regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.list-style {
  margin: 0;
  padding: 0;
  list-style: none;
}

.rouble {
  font-family: "PT Mono", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(3, 16, 27, 0.7);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 500px;
  min-height: 610px;
  padding: 40px;
  padding-top: 36px;
  background-color: #00b3ff;
  border-radius: 3px;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 15px;
  right: 15px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 10px;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal__close-btn svg {
  fill: #ffffff;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.modal__form-wrapper p {
  margin-bottom: 40px;
  line-height: 22px;
  color: #ffffff;
}
.modal__form-wrapper .modal__subtitle {
  margin: 0;
  margin-bottom: 5px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 30px;
  line-height: 42px;
}

.main-header {
  position: sticky;
  top: 0;
  z-index: 3;
  margin: 0;
  min-height: 120px;
  padding: 30px 20px 35px;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
}
@media (max-width: 767px) {
  .main-header {
    padding: 12px 10px 35px;
  }
}
@media (max-width: 1023px) {
  .main-header {
    padding: 18px 50px 35px;
    position: relative;
  }
}

.main-header__wrapper {
  display: flex;
  justify-content: space-between;
  padding: 0;
}
@media (max-width: 767px) {
  .main-header__wrapper {
    margin: 0 auto;
    width: 89.7%;
    max-width: 1024px;
  }
}
@media (max-width: 1023px) {
  .main-header__wrapper {
    padding: 0;
  }
}

.main-header__set-call {
  align-items: center;
  height: 47.1%;
  min-height: 40px;
  margin: 1px 40px 0 0;
  border-radius: 3px;
  border: 1px solid #f2f5f7;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  font-family: "Roboto Condensed", "Arial", sans-serif;
}
@media (max-width: 767px) {
  .main-header__set-call {
    display: none;
  }
}
@media (max-width: 1023px) {
  .main-header__set-call {
    margin-left: clamp(5px, 6.7%, 60px);
    display: none;
  }
}

.footer {
  background-color: #041827;
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;
  width: 89.7%;
  max-width: 1220px;
  margin: 0 auto;
  padding: 50px 0 55px 0;
}
@media (max-width: 1023px) {
  .footer__wrapper {
    flex-direction: column;
    min-width: 670px;
    width: 87%;
    padding-top: 48px;
    padding-bottom: 65px;
  }
}
@media (max-width: 767px) {
  .footer__wrapper {
    min-width: 270px;
    width: 84.3%;
    padding: 32px 0 35px 0;
  }
}

.footer__section-title {
  margin: 7px 0 22px 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 15px;
  line-height: 16px;
  font-weight: 400;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
}
@media (max-width: 1023px) {
  .footer__section-title {
    margin-top: 0;
    margin-bottom: 21px;
    background-image: none;
    letter-spacing: -0.7px;
  }
}
@media (max-width: 767px) {
  .footer__section-title {
    margin: 0;
    padding: 20px 7.5%;
    padding-top: 22px;
    background-repeat: no-repeat;
    background-size: 15px 15px;
    background-position: top 20px right 8%;
    cursor: pointer;
    letter-spacing: -0.5px;
    background-image: url("../img/svg/plus.svg");
  }
  [data-accordion-state=open] .footer__section-title {
    background-image: url("../img/svg/minus.svg");
  }
}

.footer___icons-wrapper {
  display: flex;
  flex-direction: column;
}
@media (max-width: 1023px) {
  .footer___icons-wrapper {
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 30px;
  }
}
@media (max-width: 767px) {
  .footer___icons-wrapper {
    flex-direction: column;
    width: 270px;
    margin: 0 auto;
    margin-bottom: 25px;
  }
}

.footer__social-list {
  display: flex;
  flex-wrap: wrap;
  width: 170px;
  margin-left: 10px;
}
@media (max-width: 1023px) {
  .footer__social-list {
    width: 145px;
    flex-wrap: nowrap;
  }
}
@media (max-width: 767px) {
  .footer__social-list {
    justify-content: flex-start;
    width: 150px;
    margin: 0 auto;
  }
}

.footer__links-wrapper {
  display: flex;
  justify-content: space-between;
  width: clamp(100px, 69.3%, 845px);
}
@media (max-width: 1023px) {
  .footer__links-wrapper {
    width: 100%;
  }
}
@media (max-width: 767px) {
  .footer__links-wrapper {
    position: relative;
    flex-direction: column;
  }
}

.footer__logo {
  margin-bottom: 23px;
}
@media (max-width: 1023px) {
  .footer__logo {
    margin-bottom: 0;
  }
}
@media (max-width: 767px) {
  .footer__logo {
    position: relative;
    right: 6px;
    margin: 0 auto;
    margin-bottom: 8px;
  }
}

.footer__contacts {
  width: clamp(180px, 40.8%, 345px);
  margin-left: 30px;
}
@media (max-width: 1023px) {
  .footer__contacts {
    width: 320px;
  }
}
@media (max-width: 767px) {
  .footer__contacts {
    position: relative;
    width: 100%;
    margin: 0;
  }
  .footer__contacts::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: -9.3%;
    width: 118.6%;
    height: 1px;
    background-color: rgba(248, 253, 255, 0.7);
  }
}

.footer__footer-links {
  width: clamp(386px, 55.6%, 470px);
}
@media (max-width: 1023px) {
  .footer__footer-links {
    width: 220px;
  }
}
@media (max-width: 767px) {
  .footer__footer-links {
    position: relative;
    width: 100%;
    margin: 0;
  }
  .footer__footer-links::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: -9.3%;
    width: 118.6%;
    height: 1px;
    background-color: rgba(248, 253, 255, 0.7);
  }
  .footer__footer-links::before {
    content: "";
    position: absolute;
    top: 0;
    left: -9.3%;
    width: 118.6%;
    height: 1px;
    background-color: rgba(248, 253, 255, 0.7);
  }
}

.footer__button {
  display: none;
}
@media (max-width: 767px) {
  .footer__button {
    position: absolute;
    top: 18px;
    right: 10%;
    display: block;
    width: 19px;
    height: 19px;
    padding: 2px;
    background-color: inherit;
    background-repeat: no-repeat;
    background-size: 15px 15px;
    background-position: top 2px left 2px;
    border: none;
    cursor: pointer;
  }
}

@media (max-width: 767px) {
  .footer__button-wrapper {
    position: relative;
    left: -8.9%;
    width: 118%;
    min-height: 60px;
    outline-offset: -2px;
    cursor: pointer;
  }
}
@media (max-width: 767px) and (hover: hover) {
  .footer__button-wrapper:hover:not(.focus-visible) {
    opacity: 0.7;
  }
}
@media (max-width: 767px) {
  .footer__button-wrapper.focus-visible:focus {
    opacity: 0.7;
  }
}

.footer__accordion-content--site-list {
  display: flex;
}
@media (max-width: 1023px) {
  .footer__accordion-content--site-list {
    display: block;
  }
}
@media (max-width: 767px) {
  .footer__accordion-content {
    display: none;
  }
  [data-accordion-state=open] + .footer__accordion-content {
    display: block;
  }
}

@media (max-width: 1023px) {
  .social-list__item {
    padding-top: 6px;
  }
}
.social-list__item svg {
  fill: #f8fdff;
}
.social-list__item:not(:last-child) {
  margin-right: 15px;
}
@media (max-width: 1023px) {
  .social-list__item:nth-child(3n) {
    margin-right: 0;
  }
}

.social-list__link {
  display: flex;
  width: 40px;
  height: 40px;
  justify-content: center;
  align-items: center;
}
@media (hover: hover) {
  .social-list__link:hover:not(.focus-visible) {
    opacity: 0.7;
  }
}
.social-list__link.focus-visible:focus {
  opacity: 0.7;
}

.site-list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  min-width: max-content;
  max-width: 210px;
}
.site-list--extended {
  margin-left: 30px;
}
@media (max-width: 1023px) {
  .site-list {
    min-width: 220px;
    max-width: 270px;
    max-height: max-content;
    margin-top: 23px;
  }
  .site-list--extended {
    margin: 0;
  }
}
@media (max-width: 1023px) and (max-width: 767px) {
  .site-list--extended .site-list__item:last-child {
    margin-bottom: 20px;
  }
}
@media (max-width: 767px) {
  .site-list {
    margin: 0;
  }
}

.site-list__item {
  max-width: 205px;
  margin-right: 15px;
  margin-bottom: 10px;
  line-height: 20px;
}
.site-list__item a {
  color: rgba(255, 255, 255, 0.7);
}
@media (hover: hover) {
  .site-list__item a:hover:not(.focus-visible) {
    color: #00b2ff;
