  min-height: 45px;
  padding: 14px 13px;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  text-align: center;
  color: #00b2ff;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  background-color: transparent;
  border: 1px solid #00b2ff;
  border-radius: 3px;
}
@media (max-width: 767px) {
  .info__button {
    min-width: 160px;
    min-height: 35px;
    padding: 8px 15px 9px;
  }
}

.products {
  padding-top: 66px;
  padding-bottom: 80px;
  background-color: #041827;
}
.products .container {
  padding: 0;
}
@media (max-width: 1023px) {
  .products {
    padding-left: 0;
    padding-top: 54px;
    padding-bottom: 75px;
  }
}
@media (max-width: 767px) {
  .products {
    padding-top: 46px;
    padding-bottom: 56px;
  }
}

.products__title {
  margin: 0 0 54px;
  padding: 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #ffffff;
}
@media (max-width: 1023px) {
  .products__title {
    margin: 0 0 38px;
  }
}
@media (max-width: 767px) {
  .products__title {
    margin: 0 20px 30px 0;
  }
}
@media (max-width: 767px) {
  .products__title--desktop {
    display: none;
  }
}
.products__title--mobile {
  display: none;
}
@media (max-width: 767px) {
  .products__title--mobile {
    display: block;
    font-size: 28px;
    line-height: 39px;
  }
}

.products__list {
  display: grid;
  margin: 0;
  padding: 0;
  list-style-type: none;
  gap: 85px;
  grid-template-columns: repeat(3, 1fr);
}
@media (max-width: 1023px) {
  .products__list {
    display: flex;
    flex-direction: column;
    gap: 30px;
  }
}

.products__item {
  background-color: #2b5576;
  position: relative;
  margin: 0 auto;
  max-height: calc(100% - 60px);
}

.products__link {
  display: block;
}
@media (max-width: 1023px) {
  .products__link {
    height: 250px;
  }
}

.products__heading {
  position: relative;
  top: 48px;
  left: 40px;
  z-index: 2;
  max-width: 170px;
  margin: 0;
  font-size: 20px;
  line-height: 30px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #ffffff;
}
@media (max-width: 1023px) {
  .products__heading {
    top: 38px;
    left: 45px;
    max-width: 148px;
  }
}
@media (max-width: 767px) {
  .products__heading {
    top: 25px;
    left: 30px;
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    max-width: 130px;
  }
}

.products__img {
  position: relative;
  z-index: 1;
  top: -60px;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .products__img:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.products__img.focus-visible:focus {
  opacity: 0.8;
}
.products__img img {
  width: 100%;
}
@media (max-width: 1023px) {
  .products__img img {
    height: 250px;
  }
}
.products__img::before {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 2;
  width: 60px;
  height: 60px;
  background-color: #00b2ff;
  border-radius: 3px 0 0 0;
}
@media (max-width: 767px) {
  .products__img::before {
    width: 40px;
    height: 40px;
  }
}
.products__img::after {
  content: "";
  position: absolute;
  right: 26px;
  bottom: 25px;
  z-index: 3;
  width: 6px;
  height: 10px;
  background-image: url("../img/svg/array.svg");
  background-repeat: no-repeat;
}
@media (max-width: 767px) {
  .products__img::after {
    right: 16px;
    bottom: 15px;
  }
}

.main-form {
  position: relative;
  display: flex;
  color: #ffffff;
  background-color: #00b2ff;
}

